import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import withNavBar from "@components/nav-wrapper";
import { useParams } from "react-router-dom";

import Grid from "@material-ui/core/Grid";

import AlertMessage from "@components/alert-message";
import CreateSnippet from "@components/create-snippet";
import ArticlesList from "@components/articles-list";
import { Container, Typography, Box } from "@material-ui/core";
import DeveloperCard from "@components/developer-card";
import MenuLeftPane from "@components/menu-left-pane";
import Snippets from "@components/snippets";
import ACTIONS from "@redux/actions";
import { useSelector, useDispatch } from "react-redux";
import DevelopersGrid from "@components/developers-grid";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 50,
    textAlign: "center",
  },
  leftPaneGrid: {
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },
  rightPaneGrid: {
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },
  centerArea: {},
  snippetsArea: {},
}));

function Developer({ page = "root" }) {
  let { developerId } = useParams();
  const classes = useStyles();
  const dispatch = useDispatch();

  const { userId } = useSelector((state) => {
    return {
      userId: state.profile.user.login,
    };
  });

  const [startLoading, setStartLoading] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [alertInfo, setAlertInfo] = useState({});

  useEffect(() => {
    setStartLoading(false);
    setTimeout(() => {
      setStartLoading(true);
    }, 100);
    return function cleanUp() {
      dispatch({
        type: ACTIONS.UNLOAD_DEVELOPER,
      });
    };
  }, [developerId]);

  function handleAlertCallback(alertInfo) {
    setAlertInfo(alertInfo);
    setShowAlert(true);
  }

  function handleCloseAlert() {
    setShowAlert(false);
  }

  return (
    <Container className={classes.root}>
      <Container fixed>
        {startLoading && (
          <Grid container xs={12} spacing={3}>
            <Grid className={classes.leftPaneGrid} item xs={12} sm={12} md={3}>
              <DeveloperCard developerId={developerId} />
              <br />
              <MenuLeftPane page={page} developerId={developerId} />
            </Grid>

            <Grid className={classes.centerArea} item xs={12} sm={12} md={7}>
              {(page === "snippets" || page === "root") &&
                userId === developerId && (
                  <CreateSnippet alertCallback={handleAlertCallback} />
                )}

              {(page === "snippets" || page === "root") && (
                <Snippets
                  isUser={userId === developerId}
                  developerId={developerId}
                  feed={page === "root"}
                />
              )}

              {(page === "followers" || page === "following") && (
                <DevelopersGrid developerId={developerId} type={page} />
              )}
            </Grid>

            {showAlert && (
              <AlertMessage
                handleCloseAlert={handleCloseAlert}
                {...alertInfo}
              />
            )}

            <Grid className={classes.rightPaneGrid} item xs={12} sm={12} md={2}>
              <Box
                style={{
                  marginTop: 40,
                }}
              >
                <ArticlesList />
                <Typography
                  style={{
                    textAlign: "center",
                  }}
                  variant="caption"
                  color="textSecondary"
                >
                  {"Copyright Â© "}
                  MSK Coders {new Date().getFullYear()}
                </Typography>
              </Box>
            </Grid>
          </Grid>
        )}
      </Container>
    </Container>
  );
}

export default withNavBar(Developer);
